/**axios封装
 * 请求拦截、相应拦截、错误统一处理
 */
import axios from 'axios';
import QS from 'qs';
import store from '../store/index';
import {fetchGet, fetchPost, fetchPostX} from '@/utils/fetchUtil';

const TOKEN = 'token';

const LiveBaseUrl = 'http://www.sharkyou.fun:9090'
let WSUrl = '';
// 环境的切换
if (process.env.NODE_ENV === 'development') {
  axios.defaults.baseURL = 'http://localhost:18999';
  WSUrl = 'ws://127.0.0.1:18989'
} else if (process.env.NODE_ENV === 'production') {
  let port = '';
  let wsPort = ':8989';
  if (window.location.protocol === 'https:') {
    wsPort = ':8443'
  }
  axios.defaults.baseURL = window.location.protocol + "//www.sharkyou.fun" + port;
  WSUrl = window.location.protocol.replace("http", "ws") + "//www.sharkyou.fun" + wsPort;
}

// 请求超时时间
axios.defaults.timeout = 10000;

// 请求拦截器
axios.interceptors.request.use(
  config => {
    // 每次发送请求之前判断是否存在token，如果存在，则统一在http请求的header都加上token，不用每次请求都手动添加了
    // 即使本地存在token，也有可能token是过期的，所以在响应拦截器中要对返回状态进行判断
    const token = localStorage.getItem(TOKEN) || store.state.token;
    token && (config.headers.token = token);
    return config;
  },
  error => {
    return Promise.error(error);
  })

// 响应拦截器
axios.interceptors.response.use(
  response => {
    if (response.status === 200) {
      return Promise.resolve(response);
    } else {
      return Promise.reject(response);
    }
  },
  // 服务器状态码不是200的情况
  error => {
    if (error.response.status) {
      switch (error.response.status) {
        // 401: 未登录
        // 未登录则跳转登录页面，并携带当前页面的路径
        // 在登录成功后返回当前页面，这一步需要在登录页操作。
        case 401:
          break;
        // 403 token过期
        // 登录过期对用户进行提示
        // 清除本地token和清空vuex中token对象
        // 跳转登录页面
        case 403:
          // 清除token
          localStorage.removeItem(TOKEN);
          // 跳转登录页面，并将要浏览的页面fullPath传过去，登录成功后跳转需要访问的页面
          break;
        // 404请求不存在
        case 404:
          break;
        // 其他错误，直接抛出错误提示
        default:
      }
      return Promise.reject(error.response);
    }
  }
);

const srcGet = axios.get;

/**
 * get方法，对应get请求
 * @param {String} url [请求的url地址]
 * @param {Object} params [请求时携带的参数]
 */
function get(url, params) {
  return new Promise((resolve, reject) => {
    axios.get(url, {
      params: params
    })
      .then(res => {
        resolve(res.data);
      })
      .catch(err => {
        reject(err.data)
      })
  });
}

/**
 * POST请求, URL中的数据移到请求体中时使用
 * @param {String} url [请求的url地址]
 * @param {Object} params [请求时携带的参数]
 */
function post(url, params) {
  return new Promise((resolve, reject) => {
    axios.post(url, QS.stringify(params), {
      headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'}
    })
      .then(res => {
        resolve(res.data);
      })
      .catch(err => {
        reject(err.data)
      })
  });
}

/**
 * post方法, 能够接受表单或者json对象参数
 * @param {String} url [请求的url地址]
 * @param {Object} data [请求时携带的参数]
 */
function postBody(url, data) {
  return new Promise((resolve, reject) => {
    axios.post(url, data, {
      headers: {'Content-Type': 'application/json'}
    })
      .then(res => {
        resolve(res.data);
      })
      .catch(err => {
        reject(err)
      })
  });
}

const HttpBaseUrl = axios.defaults.baseURL;
const WSBaseUrl = WSUrl + '/ws';
export default {
  HttpBaseUrl,
  WSBaseUrl,
  LiveBaseUrl,
  srcGet,
  get,
  post,
  postBody,
  fetchGet,
  fetchPost,
  fetchPostX
};
