import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/desktop'
  },
  {
    path: '/desktop',
    component: () => import('../components/Desktop'),
    children: [
      {
        path: 'login',
        name: 'login',
        component: () => import('../components/Login/Login')
      },
      {
        path: 'task',
        name: 'task',
        component: () => import('../components/Crontab/Crontab')
      },
      {
        path: 'empty',
        name: 'empty',
        component: () => import('../components/Empty')
      }
    ]
  },
]
const router = new VueRouter({
  routes
})
export default router
