import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    count: 0,
    token: ''
  },
  mutations: {
    increment(state) {
      state.count++
    },
    setToken(state, token) {
      state.token = token;
    }
  }
})

export default store;