import QS from "qs";

const baseUrl = 'http://localhost:18999';
const TOKEN = 'token'; // 自定义token
const TIME_OUT = 10000; // 超时时间

const ContentType = { // ContentType 映射表
  json: 'application/json', // json数据格式
  form: "multipart/form-data", // 表单数据格式
  X_WFU: 'application/x-www-form-urlencoded', // 请求地址中的数据移到请求体
  download: 'application/octet-stream' // 二进制文件流格式，用于download
};

function baseFetch(url, options) {
  // 基础配置
  const baseOptions = {
    method: "GET", // 默认GET请求
    mode: 'cors', //允许跨域
    headers: {},
    cache: 'default',
    /**
     * include: 不论是不是跨域的请求总是发送请求资源域在本地的 cookies、HTTP Basic authentication 等验证信息
     * omit: 从不发送cookies
     * same-origin: 同源发送cookies
     */
    credentials: 'same-origin',
    redirect: 'follow'
  };
  options = Object.assign(baseOptions, options);
  const {method, body} = options;
  const contentType = options.headers['Content-Type'];
  if (!url.startsWith('http')) url = baseUrl + url;
  // get请求没有请求体，需要将参数拼接到url上
  if (method === 'GET' && body instanceof Object) {
    const paramsArray = [];
    Object.keys(body).forEach((key) =>
      paramsArray.push(key + "=" + encodeURIComponent(body[key]))
    );
    if (url.search(/\?/) === -1) {
      url += "?" + paramsArray.join("&");
    } else {
      url += "&" + paramsArray.join("&");
    }
    // 删除请求体属性
    delete options.body;
  }
  switch (contentType) {
    case ContentType.json:
      options.body = JSON.stringify(body);
      break;
    case ContentType.X_WFU:
      options.body = QS.stringify(body);
      break;
    case ContentType.form:
      delete options.headers['Content-Type'];
      break;
  }
  const token = localStorage.getItem(TOKEN);
  if (token) options.headers[TOKEN] = token;
// 利用 Promise.race 实现超时拦截
  return Promise.race([
    new Promise((resolve, reject) => {
      window.fetch(url, options)
        .then(res => {
          // response interceptors 状态码拦截，对应异常状态吗do something
          if (!/^([23])\d{2}$/.test(String(res.status))) {
            switch (res.status) {
              case 401:     //当前用户需要验证（一般是未登陆）
                break;   //一般可以弹出遮盖层，或者回到登陆页面
              case 403:     // 服务器理解请求，但是拒绝执行，一般是token，session过期
                localStorage.removeItem(TOKEN);
                break;
              case 404:     // 找不到页面(请求失败，资源在服务器上未找到)，可以给一个友好的提示
                break;
            }
            return Promise.reject(res)
          }
          if (res.headers.has('set-cookies')) {
            localStorage.setItem(TOKEN, res.headers.get('set-cookies'));
          }
          // return data
          let contentType = res.headers.get('Content-Type');
          let type = options.type;
          let ret;
          if (type) {
            switch (type) {
              case 'json':
                ret = res.json();
                break;
              case 'blob':
                ret = res.blob();
                break;
              case 'arrayBuffer':
                ret = res.arrayBuffer();
                break;
              case 'text':
                ret = res.text();
                break;
            }
          } else if (contentType) {
            if (contentType.startsWith(ContentType.json))
              ret = res.json();
            else if (contentType.startsWith(ContentType.download))
              ret = res.blob();
            else ret = res.text();
          } else ret = res.text();
          return ret;
        }).then(data => resolve(data))
        .catch(err => {
          reject(err);
        });
    }),
    new Promise((resolve, reject) => {
      setTimeout(() => {
        reject(new Error('request timeout'));
      }, TIME_OUT);
    })
  ]);
}

export const fetchGet = (url, params, options = {}) => {
  if (!options.headers) options.headers = {};
  if (params instanceof FormData) throw new Error('不可使用GET请求发送表单！');
  return baseFetch(url, {...options, body: params});
}

export const fetchPost = (url, params, options = {}) => {
  if (!options.headers) options.headers = {};
  if (!(params instanceof FormData)) options.headers['Content-Type'] = ContentType.json;
  return baseFetch(url, {...options, method: 'POST', body: params});
}

// POST请求, URL中的数据移到请求体中时使用
export const fetchPostX = (url, params, options = {}) => {
  if (!options.headers) options.headers = {};
  options.headers['Content-Type'] = ContentType.X_WFU;
  return baseFetch(url, {...options, method: 'POST', body: params});
}
